import React, { Component } from 'react';
import List from './List';

class Container extends Component {
	constructor(props) {
		super(props);
	}

	render() {
		return (
			<div className="container">
				<div className="container__addTask">
					<div className="container__title">Task</div>
					<input className="container__input container__input-focus" placeholder="Значение ..." />
					<button className="container__input container__button">ADD</button>
				</div>

				<List />
			</div>
		)
	}
}

export default Container;