import React, { Component } from 'react';
import Item from './Item';

class List extends Component {
	constructor(props) {
		super(props);
	}

	render() {
		let array = ["task 1", "task 2", "task 3"];
		return (
			<div className="List">
				{array.map((elem, index) => (
					<Item text={elem} />
				))}
			</div>
		)
	}
}

export default List;

