import React, { Component } from 'react';


class Item extends Component {
	constructor(props) {
		super(props);
	}

	render() {
		return (

			<div className="item">
				<div className="item__elem check">
					<label for="check__input" className="check__label"></label>
					<input type="checkbox" id="check__input" className="check__input" />
				</div>
				<div className="item__elem text">
					<input type="text" className="text__input" value={this.props.text} />
				</div>
				<div className="item__elem delete">
					<labe for="delete__input" className="delete__label"></labe>
					<input type="checkbox" className="delete__input" id="delete__input" />
				</div>
			</div>
		)
	}
}

export default Item;